import React, { useEffect, useRef, useState } from 'react';
import { SubHeading, Card } from '../../components';

const Menu = () => {
    const titleRef = useRef(null);
    const cardRef = useRef(null);
    const [isExpanded, setIsExpanded] = useState(false);

    useEffect(() => {
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        entry.target.classList.add('animate-scaleIn', 'opacity-100');
                        entry.target.classList.remove('opacity-0', 'translate-y-10');
                    }
                });
            },
            { threshold: 0.1 }
        );

        if (titleRef.current) observer.observe(titleRef.current);
        if (cardRef.current) observer.observe(cardRef.current);

        return () => observer.disconnect();
    }, []);

    return (
        <div 
            className={`relative transition-all duration-500 ease-in-out cursor-pointer
                      ${isExpanded ? 'h-auto px-24 py-16 sm:px-4' : 'h-64 px-12 py-8'} 
                      sm:-mt-12 overflow-hidden bg-orange-100
                      border-y-4 border-amber-900`}
            style={{
                backgroundImage: `
                    linear-gradient(45deg, #FFA07A 25%, transparent 25%),
                    linear-gradient(-45deg, #FFA07A 25%, transparent 25%),
                    linear-gradient(45deg, transparent 75%, #FFA07A 75%),
                    linear-gradient(-45deg, transparent 75%, #FFA07A 75%)
                `,
                backgroundSize: '20px 20px',
                backgroundPosition: '0 0, 0 10px, 10px -10px, -10px 0px'
            }}
            id="menu"
            onClick={() => setIsExpanded(!isExpanded)}
            onMouseEnter={() => setIsExpanded(true)}
            onMouseLeave={() => setIsExpanded(false)}
        >
            <div className="absolute inset-0 bg-gradient-to-b from-orange-200 to-amber-100 opacity-95"></div>

            <div 
                ref={titleRef}
                className={`relative mb-12 text-center transition-all duration-700
                          ${isExpanded ? 'opacity-100 scale-100' : 'opacity-70 scale-95'}`}
            >
                <SubHeading title="Notre Menu" className="text-amber-900 text-3xl mb-6" />
                <h2 className="font-heading text-amber-950 text-5xl font-bold tracking-[0.3rem] sm:text-4xl
                             hover:text-amber-800 transition-colors duration-300 
                             border-b-4 border-amber-900 inline-block pb-4 mb-6">
                    Saveurs d'Afrique de l'Ouest
                </h2>
                <p className="mt-6 text-amber-950 font-body text-2xl sm:text-xl font-normal italic">
                    Découvrez nos plats traditionnels préparés avec amour
                </p>
            </div>
            
            <div 
                ref={cardRef}
                className={`relative transition-all duration-700 delay-300 
                          bg-white/60 backdrop-blur-sm rounded-xl p-8 shadow-lg
                          ${isExpanded ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}
            >
                <Card />
            </div>
        </div>
    );
};

export default Menu;